import React from 'react';
import { MuiThemeProvider } from '@material-ui/core/styles';
import { darkTheme } from 'core/theme';
import { SecondaryAppBar, SecondaryToolbar, SecondaryTabs } from './styles';
const SecondaryNav = ({ children, tabs = false, ...tabProps }) => (<MuiThemeProvider theme={darkTheme}>
    <SecondaryAppBar>
      {tabs ? (<SecondaryTabs indicatorColor="primary" {...tabProps}>
          {children}
        </SecondaryTabs>) : (<SecondaryToolbar>{children}</SecondaryToolbar>)}
    </SecondaryAppBar>
  </MuiThemeProvider>);
export default SecondaryNav;
